.containerGallery{
    -webkit-column-count: 3;
    -moz-column-count: 3;
    column-count: 3;
    -webkit-column-width: 33%;
    -moz-column-width: 33%;
    column-width: 33%;
    padding: 0 12px;
}

.imageCard{
    -webkit-transition: all 350ms ease;
    transition: all 350ms ease;
    animation: leave .8s linear;
    cursor: pointer;
    margin-bottom: 12px;
    outline: 1px solid rgba(255, 255, 255, 0.6);
    outline-offset: -4px;
}

.image{
    width: 100%;
}

.imageCard:hover{
    filter: brightness(30%);
}

@media (max-width: 992px){
    .containerGallery{
        -webkit-column-count: 2;
        -moz-column-count: 2;
        column-count: 2;
        
    }
}

@media (max-width: 480px){
    .containerGallery{
        -webkit-column-count: 1;
        -moz-column-count: 1;
        column-count: 1;
        -webkit-column-width: 100%;
        -moz-column-width: 100%;
        column-width: 100%;
    }
}

@keyframes leave{
    0%{
        transform: scale(0.3);
        -webkit-transform: scale(0.3);
    }
    100%{
        transform: scale(1.0);
        -webkit-transform: scale(1.0);
    }
}